.banner {
  background: #00BCD4;
  height: 80px;
  padding-left: 20px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999999;
  width: 100%;
  cursor: pointer; }
  .banner {
    *zoom: 1; }
  .banner:before, .banner:after {
    content: "";
    display: table; }
  .banner:after {
    clear: both; }
  .banner .logo {
    float: left;
    margin: 20 10px;
    padding: 2px;
    height: 40px;
    cursor: pointer; }
    .banner .logo img {
      height: 100%; }
  .banner .header {
    float: left; }
    .banner .header {
      *zoom: 1; }
    .banner .header:before, .banner .header:after {
      content: "";
      display: table; }
    .banner .header:after {
      clear: both; }
    .banner .header .header-text {
      float: left;
      margin: 20 10px;
      font-size: 20px;
      padding: 10px;
      font-family: 'Montserrat', sans-serif;
      font-weight: bold;
      color: #FFFFFF; }

.home-page {
  background: #FFFFFF;
  padding: 40px;
  cursor: crosshair;
  *zoom: 1; }
  .home-page:before, .home-page:after {
    content: '';
    display: table; }
  .home-page:after {
    clear: both; }
  .home-page .people-container {
    width: 75%;
    margin: 0 auto; }
    .home-page .people-container .people-wrapper {
      *zoom: 1;
      float: left;
      clear: none;
      text-align: inherit;
      width: 31.33333%;
      margin-left: 0%;
      margin-right: 3%;
      margin-bottom: 20px; }
      .home-page .people-container .people-wrapper:before, .home-page .people-container .people-wrapper:after {
        content: '';
        display: table; }
      .home-page .people-container .people-wrapper:after {
        clear: both; }
      .home-page .people-container .people-wrapper:nth-child(3n) {
        margin-right: 0%;
        float: right; }
      .home-page .people-container .people-wrapper:nth-child(3n+1) {
        clear: both; }
      .home-page .people-container .people-wrapper .person {
        background: rgba(0, 188, 212, 0.6);
        color: #FFFFFF;
        opacity: 0;
        transition: opacity 500ms;
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center; }
        .home-page .people-container .people-wrapper .person.Brandon {
          background: rgba(244, 67, 54, 0.75); }
        .home-page .people-container .people-wrapper .person.Patrick {
          background: rgba(156, 39, 176, 0.75); }
        .home-page .people-container .people-wrapper .person.Neil {
          background: rgba(76, 175, 80, 0.75); }
        .home-page .people-container .people-wrapper .person.Davinci {
          background: rgba(255, 152, 0, 0.75); }
        .home-page .people-container .people-wrapper .person.Shota {
          background: rgba(63, 81, 181, 0.75); }
        .home-page .people-container .people-wrapper .person.Janel {
          background: rgba(233, 30, 99, 0.75); }
        .home-page .people-container .people-wrapper .person.Samantha {
          background: rgba(0, 150, 136, 0.75); }
        .home-page .people-container .people-wrapper .person.Elizabeth {
          background: rgba(251, 209, 51, 0.75); }
        .home-page .people-container .people-wrapper .person.Allan {
          background: rgba(0, 135, 188, 0.75); }
        .home-page .people-container .people-wrapper .person .name {
          position: relative;
          top: 50%;
          -webkit-transform: translateY(-50%);
          -moz-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
          transform: translateY(-50%);
          font-family: "Roboto Slab", "Roboto", Helvetica, Arial, sans-serif;
          color: #333333;
          font-size: 32px;
          font-size: 2rem;
          line-height: 64px;
          line-height: 4rem;
          color: white;
          font-family: 'Open Sans', sans-serif;
          font-weight: bold; }
      .home-page .people-container .people-wrapper .prof {
        width: 100%; }
        .home-page .people-container .people-wrapper .prof img {
          width: 100%; }
      .home-page .people-container .people-wrapper:hover .prof {
        opacity: 0.5; }
      .home-page .people-container .people-wrapper:hover .person {
        opacity: 1; }

.member-page {
  cursor: crosshair; }

.member-container {
  width: 80%;
  margin: auto;
  overflow: hidden; }
  .member-container .member {
    display: inline-block;
    margin: 50 50 100 50; }
  .member-container .circular img {
